@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  font-family: 'Open Sans', sans-serif;

}



.header-headline{
  margin: 0;
  font-family: cursive;
  font-weight: 100;
}

.app-header-logo{
  width:50px;
}

.contact-preview{
  display:flex;
}
.contact-img{
  width: 90px;
  object-fit: cover;
  border-radius: 50%;
  margin: 10px 20px 0 0;
}


footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color: white;
}



